}

.user-logo {
  height: 100%;
  width: 100%;
  border-radius: 4px;
  background-color: #2fbfbe;
  padding: 4px;
  box-sizing: border-box;
  line-height: 100%;
  text-align: center;
  vertical-align: middle;
}

.user-logo label {
  color: white;
  font-weight: bold;
  font-size: 16px;
  line-height: 30px;
}
